"use client";

export default function ProgressTabs({ stepIndex = 0, steps = [] }) {
  const percent = Math.round(((stepIndex + 1) / steps.length) * 100);

  return (
    <div>
      <div className="flex items-center justify-between gap-3 mb-4">
        {steps.map((label, i) => (
          <div key={i} className="flex-1 text-center">
            <div className={`mx-auto w-10 h-10 rounded-full flex items-center justify-center text-sm font-semibold
              ${i <= stepIndex ? "bg-emerald-500 text-white" : "bg-gray-200 text-gray-600"}`}>
              {i + 1}
            </div>
            <div className="text-xs mt-2 text-gray-600">{label}</div>
          </div>
        ))}
      </div>

      <div className="w-full bg-gray-200 h-2 rounded">
        <div className="h-2 rounded bg-emerald-400 transition-all" style={{ width: `${percent}%` }} />
      </div>
    </div>
  );
}
