"use client";

import { useState, useEffect } from "react";

export default function PhotoUploader({ file, onChange }) {
  const [preview, setPreview] = useState(null);

  useEffect(() => {
    if (file) {
      const url = URL.createObjectURL(file);
      setPreview(url);
      return () => URL.revokeObjectURL(url);
    }
  }, [file]);

  return (
    <div className="flex flex-col items-center gap-4">
      <div className="w-48 h-48 rounded-xl bg-gray-100 flex justify-center items-center overflow-hidden">
        {preview ? (
          <img src={preview} className="w-full h-full object-cover" />
        ) : (
          <span className="text-gray-400">No Image</span>
        )}
      </div>

      <label className="px-5 py-3 bg-gray-800 text-white rounded cursor-pointer">
        Upload Photo
        <input
          type="file"
          accept="image/*"
          className="hidden"
          onChange={(e) => onChange(e.target.files[0])}
        />
      </label>

      <label className="px-5 py-3 bg-indigo-600 text-white rounded cursor-pointer">
        Take Picture
        <input
          type="file"
          accept="image/*"
          capture="environment"
          className="hidden"
          onChange={(e) => onChange(e.target.files[0])}
        />
      </label>
    </div>
  );
}
