"use client";

import AnimatedInput from "./AnimatedInput";
import PhotoUploader from "./PhotoUploader";

/**
 * Renders one step (single question) based on step.type
 * props:
 * - step: { key, label, type, options, placeholder, required, ... }
 * - value, onChange, error
 */
export default function StepCard({ step, value, onChange, error, onTakePhoto }) {
  const type = step.type;

  if (type === "photo") {
    return (
      <div className="bg-white rounded-2xl shadow-lg p-6">
        <h2 className="text-xl font-semibold mb-3">{step.label}</h2>
        <p className="text-sm text-gray-500 mb-4">Use camera for a clear image. Try natural light and show the thinning area.</p>

        <PhotoUploader file={value} onChange={(f) => { onChange(f); if (onTakePhoto) onTakePhoto(f); }} />

        {error && <p className="text-red-600 text-sm mt-3">{error}</p>}
      </div>
    );
  }

  if (type === "stages") {
    return (
      <div className="bg-white rounded-2xl shadow-lg p-6">
        <h2 className="text-xl font-semibold mb-4">{step.label}</h2>
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
          {step.options.map((opt) => (
            <div
              key={opt.id}
              onClick={() => onChange(opt.id)}
              className={`cursor-pointer rounded-xl border p-4 text-center ${value === opt.id ? "ring-2 ring-emerald-300" : "border-gray-200"}`}
            >
              <div className="aspect-[4/3] mb-3 bg-gray-50 rounded overflow-hidden flex items-center justify-center">
                <img src={opt.img} alt={opt.title} className="object-contain w-3/4 h-3/4" />
              </div>
              <div className="font-medium">{opt.title}</div>
            </div>
          ))}
        </div>
        {error && <p className="text-red-600 text-sm mt-3">{error}</p>}
      </div>
    );
  }

  // generic text/select/number/radio
  return (
    <div className="bg-white rounded-2xl shadow-lg p-6">
      <h2 className="text-xl font-semibold mb-3">{step.label}</h2>

      <AnimatedInput
        type={type}
        options={step.options}
        value={value}
        placeholder={step.placeholder}
        onChange={onChange}
      />

      {error && <p className="text-red-600 text-sm mt-3">{error}</p>}
    </div>
  );
}
